import styled from 'styled-components';

export const Main = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;

  background: blue;
`;

export const Logo = styled.div`
  display: flex;
  justify-content: center;
  /* position: absolute; */
  width: 100%;

  /* @media (max-width: 768px) {
    justify-content: center;
    position: relative;
  } */
`;

export const Content = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  flex-grow: 1;

  & *:not(img) {
    width: 379px;
    font-family: 'Poppins';
    font-style: normal;
    color: #E0E0E0;
  }

  & .logo {
    visibility: hidden;

    @media (max-width: 768px) {
      visibility: visible;
    }

  }

  & h1 {
    font-weight: 400;
    font-size: 60px;
    line-height: 90px;
  }

  & h2 {
    font-weight: 400;
    font-size: 30px;
    line-height: 45px;
  }

  & h3 {
    width: 301px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }

  @media (max-width: 768px) {
    width: 100%;
  }
`;

export const Image = styled.div`
  width: 50%;
  overflow: hidden;
  background-image: url(${(props) => props.src});

  @media (max-width: 768px) {
    display: none;
  }
`;

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-grow: 1;
  max-height: 500px;

  & label {
    position: relative;
  }

  & label img {
    position: absolute;
    width: 20px;
    right: 20px;
    top: 16px;
  }

  & input {
    height: 60px;
    background: #26292C;
    border: 1px solid #FFFFFF;
    border-radius: 50px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    padding: 20px;
    padding-right: 45px;

    ::placeholder {
      color: #E0E0E0;
    }
  }

  & input[type='password']:not(:placeholder-shown) {
    font-family: Verdana;
    font-size: 25px;
    letter-spacing: 0.125em;
  }

  & p {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #E9B425;
    visibility: hidden;
  }

  & button {
    height: 67px;
    background: linear-gradient(90deg, #FF2D04 0%, #C13216 100%);
    box-shadow: inset 5px 5px 15px rgba(0, 0, 0, 0.15), 5px 5px 15px 0px black;
    border-radius: 50px;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    color: #FFFFFF;
    border: none;
  }
`;

export const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;