import React from "react";
import { Input } from "./style";

type InputProps = {
  type: string;
  placeholder: string;
  icon: string;
}

const LoginInput = ({type, placeholder, icon}: InputProps) => {
  return (
    <Input>
      <input type={type} placeholder={placeholder} />
      <img src={icon} />
    </Input>
  );
};

export default LoginInput;